import React, { Fragment } from "react";
import clsx from "clsx";
import * as st from "./header.module.css";
import { Link } from "gatsby";
import { Container } from "../container";
import { useQueryDataHeader } from "./use-query-data-header";
import { usePurchases } from "src/contexts/purchases-context";
import { BasketButton } from "src/components/ui/basket-button";

export const Header = ({ className, ...rest }) => {
  const [links, brandLink] = useQueryDataHeader();
  const { state } = usePurchases();

  return (
    <header className={clsx(st.header, className)} {...rest}>
      <Container>
        <nav className={st.navigation}>
          <ul className={st.navigation__list}>
            {links.map((link, idx) => (
              <Fragment key={link.name}>
                <li className={st.navigation__item}>
                  <Link
                    className={st.navigation__link}
                    activeClassName={st.navigation__link_active}
                    to={link.url}
                  >
                    {link.name}
                  </Link>
                </li>
                {idx !== links.length - 1 && (
                  <div className={st.navigation__divider} />
                )}
              </Fragment>
            ))}
          </ul>
          <div className={st.navigation__item}>
            <Link
              className={clsx(st.navigation__link, st.navigation__link_active)}
              to={brandLink.url}
            >
              {brandLink.name}
            </Link>
            <BasketButton
              type="menu"
              value={state.length}
              className={st.basketButton}
            />
            {/* <button style={{ marginLeft: "10px" }}>{state.length}</button> */}
          </div>
        </nav>
      </Container>
    </header>
  );
};
