import React from "react";
import clsx from "clsx";
import * as st from "./footer.module.css";
import { Container } from "../container";
import { useQueryDataFooter } from "./use-query-data-footer";

export const Footer = ({ className, ...rest }) => {
  const blocks = useQueryDataFooter();

  return (
    <footer className={clsx(st.footer, className)} {...rest}>
      <Container className={st.container}>
        {blocks.map((block, idx) => (
          <div key={idx}>
            {block.map(
              ({ lineHead, lineStyle, lineBody, lineSeparator }, idx) => (
                <p
                  key={idx}
                  className={clsx(st.line, {
                    [st.line_light]: lineStyle === "light" ? true : false,
                    [st.line_dark]: lineStyle === "dark" ? true : false,
                  })}
                >
                  {lineHead && (
                    <span className={st.line__head}>{lineHead}</span>
                  )}
                  {lineSeparator}
                  {lineBody}
                </p>
              )
            )}
          </div>
        ))}
      </Container>
    </footer>
  );
};
