import { useStaticQuery, graphql } from "gatsby";

export const useQueryDataFooter = () => {
  const data = useStaticQuery(graphql`
    query {
      dataJson {
        footer {
          blocks {
            lineHead
            lineSeparator
            lineStyle
            lineBody
          }
        }
      }
    }
  `);

  return data.dataJson.footer.blocks;
};
