import { useStaticQuery, graphql } from "gatsby";

export const useQueryDataHeader = () => {
  const {
    dataJson: { header },
  } = useStaticQuery(graphql`
    query {
      dataJson {
        header {
          brandLink {
            name
            url
          }
          links {
            name
            url
          }
        }
      }
    }
  `);

  return [header.links, header.brandLink];
};
